import { notFound } from 'next/navigation';
import { getRouteComponent, getRouteMetadata } from '@/shared/lib/router';

import { HomePage } from '@/pages-layer';

const PAGE_COMPONENTS = {
  HomePage,
} as const;

interface DynamicPageProps {
  params: {
    slug?: string[];
  };
}

export default function DynamicPage({ params }: DynamicPageProps) {
  const path = params.slug ? `/${params.slug.join('/')}` : '/';
  const componentName = getRouteComponent(path);
  
  if (!componentName) {
    notFound();
  }
  
  // Get the actual component
  const PageComponent = PAGE_COMPONENTS[componentName as keyof typeof PAGE_COMPONENTS];
  
  if (!PageComponent) {
    notFound();
  }
  
  // Get route metadata for SEO
  const metadata = getRouteMetadata(path);
  
  return <PageComponent />;
}

export function generateStaticParams() {
  return [
    { slug: [] }, // Home page
    { slug: ['login'] },
    { slug: ['register'] },
    { slug: ['dashboard'] },
    { slug: ['profile'] },
    { slug: ['settings'] },
  ];
}

export async function generateMetadata({ params }: DynamicPageProps) {
  const path = params.slug ? `/${params.slug.join('/')}` : '/';
  const metadata = getRouteMetadata(path);
  
  if (!metadata) {
    return {
      title: 'Page Not Found',
    };
  }
  
  return {
    title: metadata.title,
    description: metadata.description,
  };
}
